<template>
  <div class="map-container" ref="container">
    <div class="map-tooltip" :style="{
      bottom, left, visibility: visibility ? 'visible' : 'hidden'
    }">
      <div class="tooltip-title tooltip-row">
        <div class="tooltip-row flex-grow">
          <img class="tooltip-icon" src="@/assets/map/build.png" alt="" srcset="">
          楼栋信息
        </div>
        <div style="font-size: 20px;">
          {{ name }}
        </div>
      </div>
      <div class="tooltip-row">
        <div class="row-key">楼栋长</div>
        <div class="row-val">伊关离园</div>
      </div>
      <div class="tooltip-row">
        <div class="tooltip-row flex-grow">
          <div class="row-key">住户</div>
          <div class="row-val">30</div>
        </div>
        <div class="tooltip-row flex-grow">
          <div class="row-key">居民</div>
          <div class="row-val">120</div>
        </div>
        <div class="tooltip-row flex-grow">
          <div class="row-key">党员数</div>
          <div class="row-val">10</div>
        </div>
      </div>
      <div class="tooltip-title tooltip-row">
        <div class="tooltip-row">
          <img class="tooltip-icon" src="@/assets/map/star.png" alt="" srcset="">
          社区关爱
        </div>
      </div>
      <div class="tooltip-row">
        <div class="tooltip-row flex-grow">
          <div class="row-key">预警人员</div>
          <div class="row-val">30</div>
        </div>
        <div class="tooltip-row flex-grow">
          <div class="row-key">关爱人数</div>
          <div class="row-val">60</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { initMap } from '@wuch96/cesium-zhaomei'
import '@wuch96/cesium-zhaomei/dist/style.css'
export default {
  name: 'Map',
  data() {
    return {
      bottom: '0px',
      left: '0px',
      name: '',
      visibility: false,
    }
  },
  mounted() {
    const instance = initMap(this.$refs.container)
    //const gltfUrl = 'https://103.91.210.232:13890/output/community.gltf'; //初始nginx节点
    const gltfUrl = 'https://61.139.65.190:44763/output/community.gltf'; //成都nginx节点
    //const gltfUrl = './src/assets/gltf/community.gltf'; //本地gltf资源
    instance.loadGltf(gltfUrl, {
      position: { lat: 28.02639490282782, lng: 102.8603379307258, h: 0 },
      scale: 8,
      hpr: { heading: -181, pitch: 0, roll: 0 }
    })
    instance.observer.on('move', (payload) => {
      if (payload == null || payload?.name.startsWith('group')) {
        this.visibility = false
      } else {
        this.visibility = true
        this.bottom = payload.bottom
        this.left = payload.left
        this.name = payload.name
      }
    })
  },
  beforeDestroy() {
  }
}
</script>
<style lang="less">
.map-container {
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: 0px;
  top: 0px;
  background-color: grey;

  .map-tooltip {
    position: absolute;
    z-index: 99;
    width: 294px;
    border: 1px solid;
    background: linear-gradient(90deg, rgba(72, 64, 42, 0.4) 0%, rgba(61, 61, 61, 0) 100%);
    border-image: linear-gradient(270deg, rgba(255, 208, 59, 1), rgba(255, 208, 59, 0.2)) 1 1;
    box-shadow: 0px 0px 20px 0px rgba(255, 208, 59, 0.2);
    pointer-events: none;
    color: white;
    padding: 16px;
    text-wrap: nowrap;
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  .tooltip-title {
    font-family: 'titleFont';
    font-size: 18px;
    font-weight: 400;
    text-shadow: 0px 0px 9px rgba(255, 208, 59, 0.7);
  }

  .tooltip-row {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;

    .row-key {
      color: rgba(255, 255, 255, 0.8);
      font-weight: 400;
      margin-right: 8px;
    }

    .row-val {
      color: white;
      font-weight: 500;
    }
  }

  .flex-grow {
    flex-grow: 1;
  }

  .tooltip-icon {
    width: 32px;
    height: 32px;
  }
}
</style>
